@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&family=Raleway:wght@300;700&display=swap);
.showcase-section{
    height: 100vh;
    background: linear-gradient(to top, var(--gradient-blue),  var(--gradient-red));
    color: var(--white);
}


.showcase-content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 100vh;
}
.showcase-image {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 95%;
    overflow: hidden;
    grid-gap: 0.2rem;
    gap: 0.2rem;
    padding: 4rem 0;

}
.box {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    overflow: hidden;
    border-radius: 30px;
    background-position: center;
    background-size: cover;
    opacity: 0.8;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    transition: all 0.3s ease;
}
.box:hover{
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
.box-01{
    background-image: url(/framed/home/static/media/about_01.a4448959.jpg);
}
.box-02{
    background-image: url(/framed/home/static/media/about_02.579948dc.jpg);
}
.box-03{
    background-image: url(/framed/home/static/media/about_03.1f86401e.jpg);
    grid-row: 1/ 3;
}

.about-benefits{
    height: 50%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 1rem 2rem;
    grid-gap: 2rem;
    gap: 2rem;
    
}
.benefit {
    width: 80%;
}
.benefit h2{
    color: var(--red);
    font-size: 36px;
    font-family: 'Raleway', sans-serif;

}
.header{
    height: 100vh;
    background: linear-gradient(to top, var(--gradient-red), var(--gradient-blue)) ;
    color: var(--white);
    display: grid;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.hero{
    display: grid;
    grid-template-columns: 1fr 1fr;
    -webkit-align-items: center;
            align-items: center;
}
.hero-titles h1{
    font-size: 64px;
    font-family: 'Raleway', sans-serif;
}

.hero-titles span{
    color: var(--red);
}
.hero-titles p {
    font-size: 36px;
    color: var(--grey);
}

.hero-image{
    display: grid;
    -webkit-justify-content: center;
            justify-content: center;
}
.hero-image img{
    height: 494px;
    border-radius: 2rem;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    opacity: 0.8;    
    transition: all 0.3s ease-out;
}
.hero-image img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1; 
}

.about-section{
    height: 100vh;
    background: linear-gradient(to top,  var(--gradient-red), var(--gradient-blue));
    color: var(--white);
}
.about{
    /* border: 1px solid red; */
    height: 100vh;
    display: grid;
    padding: 1rem;
}
.about h1{
    color: var(--red);
    font-size: 36px;
    margin-bottom: 1rem;
}
.about-content{
    -webkit-align-self: center;
            align-self: center;
}
.about-footer{
    -webkit-align-self: end;
            align-self: end;
    height: 90px;
    border-top: 1px solid var(--grey);
    text-align: center;
}

.about-footer p {
    margin-top: 2rem;
    color: var(--grey);
}
.navbar{
    display: grid;
    grid-auto-flow: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    position: fixed;

    justify-self: center;
    height: 70px;
    width: 100%;
    border-bottom: 1px solid var(--grey);
    padding: 0 2rem;
    z-index: 99;
    transition: all 0.5s ease;

}
.navbar a,
.navhidden a{
    color: var(--grey);
    text-decoration: none;
    padding: 0 1rem;
}
.navbar a:hover,
.navhidden a:hover{
    color: var(--white);
}
.navhidden {
    display: grid;
    grid-auto-flow: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    position: fixed;

    justify-self: center;
    height: 70px;
    width: 100%;
    border-bottom: 1px solid var(--grey);
    padding: 0 2rem;
    z-index: 99;
    transition: all 0.5s ease;
    background-color: #181414d3;
}


:root{
    --red: #F51036;
    --white: #EEEEEE;
    --grey: #A3A3A3;
    --gradient-blue: #1C1D29;
    --gradient-red: #181414;
}


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-family: 'Open Sans', sans-serif;
    position: relative;
}
.container{
    width: 1366px;
    margin: 0 auto;
}



